h2, h3 {
    color: white
}

.about-page {
    margin: auto;
    width: 50%;
}

.project-page{
    /* background-color: black; */
    /* height: 100vh; */
    width: 80%;
}

#image-track {
    display: flex;
    gap: 4vmin;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(0%, -50%);
    margin: 0rem;
    overflow: hidden;
}

#image-track > .image {
    width: 40vmin;
    height: 56vmin;
    object-fit: cover;
    object-position: 100% 50%;
    border-radius: 10px;
    /* box-shadow: 0 5px 5px rgba(255, 255, 255, 0.2); */
}

.no-select {
    user-select: none;
  }

.fullscreen-overlay {
    position: fixed;
    top: 2;
    left: 0;
    width: 100%;
    /* height: 100%; */
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999; 
    
}

.fullscreen-image {
    max-width: 1895px;
    max-height: 800px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.8);
}